<template>

    <div class="w-full h-full flex flex-col items-center gap-5">
        <div class="mt-10 reportshead">{{$store.state.Translate["myreports"]}}</div>
        <div class="w-[90%] h-[4%]  rounded-md listsearchcont flex">
            <input type="text" class="w-[80%] h-full bg-transparent outline-none ml-5 listsearchinput" v-model="text" :placeholder="$store.state.Translate['searchmyreports']" name="search">
            <div class="w-[20%] h-full flex justify-center items-center">
                <i class="fas fa-search"></i>
            </div>
        </div>
        <div  class="w-[90%] h-[70%]  mt-3 overflow-auto"  >
              <div v-for="(item, index) in GetFilteredResult ()" class="w-full h-[8%] rounded-md mt-3  reportlist flex justify-between" @click="SetPage(index)" :class="{'selected': $store.state.SelectedMyReport == index}" v-bind:id="item.Page">
                <p class="itemlabel ml-3 flex items-center w-[90%] h-full">{{ item.Title }}</p>  
                <p class="itemlabel ml-2 flex items-center w-[25%] h-full">&#x2022 {{ item.Type }}</p>
              </div>
        </div>
    </div>
</template>


<script>

export default {
  methods: {
    test(){
      this.$store.state.active = !this.$store.state.active
    },
    SetPage(index) {
      if (this.$store.state.SelectedMyReport == index) {
        this.$store.state.Page = 'Player'
        this.$store.state.SelectedMyReport = -1
      } else {
        this.$store.state.Page = 'ReportData'
        this.$store.state.SelectedMyReport = index
      }
    },
    GetFilteredResult () {
      if (this.text == '') {
        return this.$store.state.MyReports
      }
      return this.$store.state.MyReports.filter(data => data.Title.includes(this.text))
    }
  },
  data () {
    return {
      text : "",
      
    }
  },
  name: 'App',
  components: {
    
  }
  
  
}
</script>

<style>
::-webkit-scrollbar {
  display: none;
}
    .reportshead {
        font-family: 'Proxima Nova';
        font-style: normal;
        font-weight: 700;
        font-size: 1.8vh;
        letter-spacing: 0.03em;
        color: #FFFFFF;
    }
    .listsearchcont {
        background: rgba(217, 217, 217, 0.07);
    }
    .listsearchinput::placeholder {
        color: rgba(255, 255, 255, 0.22);
    }
    .listsearchinput {
        font-family: 'Proxima Nova';
        font-style: normal;
        font-weight: 700;
        font-size: 1.2vh;
        letter-spacing: 0.03em;
        color: rgba(255, 255, 255, 0.22);
    }
    .listsearchcont i {
        color :#828282;
        font-size: 1.4vh;
        transition: color 0.2s ease-in-out;
    }
    .listsearchcont i:hover {
      color: white;
    }
    .reportlist[id="Player"] {
        background: rgba(0, 209, 255, 0.08);
    }
    .reportlist[id="Player"] > p {
      color: #00D1FF;
    }
    .reportlist[id="Other"] {
      background: rgba(0, 255, 71, 0.08);
    }
    .reportlist[id="Other"] > p {
      color: #00FF47;
    }
    .reportlist[id="Bugs"] {
      background: rgba(255, 61, 0, 0.08);
    }
    .reportlist[id="Bugs"] > p {
      color: #FF3D00;
    }
    .itemlabel {
      font-family: 'Proxima Nova';
      font-style: normal;
      font-weight: 700;
      font-size: 1.2vh;
      letter-spacing: 0.03em;
      color: #00FF47;
    }
    .selected {
      border: 1px solid #000000;
    }
</style>