<template>
  <div @click="test" class="w-screen h-screen  flex items-center flex-col gap-8 bg-[url(@/assets/report-overlay.png)] bg-cover bg-no-repeat">
    <!-- <div class="w-full h-full bg-[url(@/assets/reference.png)] bg-cover bg-no-repeat opacity-30 absolute z-0"></div>  -->
      <div class="topnav w-[95%] h-[8.5%]  flex justify-between items-center header  ">
          <div class="serverimage w-[20%] h-full  flex  items-center">
              <img src="./assets/servername.png">
          </div>
          <div class="w-[22.5%] h-full border-b-4 border-[#FFFFFF] flex justify-center items-center">
              <div v-if="$store.state.UserType == 'user'" class="header-text">{{$store.state.Translate['reportsubmition']}}</div>
              <div v-if="$store.state.UserType == 'admin'" class="header-text">{{$store.state.Translate['reports']}}</div>
          </div>
          <div class="w-[20%] h-full  flex justify-end items-center gap-5">
              <div class="w-[60%] h-[50%]  flex flex-col items-end justify-center mt-2">
                  <p class="charname">{{$store.state.PlayerData.Name}}</p>
                  <p v-if="$store.state.UserType == 'user'" class="char-id">{{$store.state.PlayerData.Id}}</p>
                  <p v-if="$store.state.UserType == 'admin'" class="char-id-admin">{{$store.state.Translate['admin']}}</p>
              </div>
              <div class="w-[15%] h-[60%] border-[3px] border-[#FFFFFF] overflow-hidden rounded-full ">
                  <img :src="image">
              </div>
          </div>
      </div>
      <div v-if="$store.state.CloseReport" class="absolute w-[90%] h-[85%] mt-[12.5vh]  flex justify-center items-center " >
          <div class="w-[28%] h-[24%]  ml-[22.5vh] closerepbg flex flex-col items-center justify-evenly gap-[2vh]" style="z-index:99999; margin-right:18vh">
              <p class="closetitle">{{$store.state.Translate['closerepr']}}</p>
              <p class="reportid">{{$store.state.ReportList[$store.state.SelectedMyReport].Type}}</p>
              <div class="w-full h-[5.5vh]  flex justify-center gap-[4vh] items-center">
                  <div class="h-full w-[25%] flex justify-center items-center interactbuttons" @click="CloseReportServer">{{$store.state.Translate['yes']}}</div>
                  <div class="h-full w-[25%] flex justify-center items-center interactbuttons" @click="CloseReport">{{$store.state.Translate['no']}}</div>
              </div>
          </div>
      </div>
      <div v-if="$store.state.AddPlayer" class="absolute w-[90%] h-[85%] mt-[12.5vh]  flex justify-center items-center " >
        <div class="w-[28%] h-[24%]  ml-[22.5vh] closerepbg flex flex-col items-center justify-evenly gap-[2vh]" style="z-index:99999; margin-right:18vh">
            <p class="closetitle">Add Player To Report</p>
            <input type="number" class="w-[25%] bg-inherit border-transparent outline-0 idval " v-model="playerid"   oninput="validity.valid||(value='');" pattern="[0-9]" required minlength="3" maxlength="15" size="15" placeholder="ID" style="color: #9E9E9E; font-family: Gilroy; text-align:center">
            <div class="w-full h-[5.5vh]  flex justify-center gap-[4vh] items-center">
                <div class="h-full w-[25%] flex justify-center items-center interactbuttons-add" @click="AddPlyServer">{{$store.state.Translate['add']}}</div>
                <div class="h-full w-[25%] flex justify-center items-center interactbuttons-add" @click="CloseAddPly">{{$store.state.Translate['cancel']}}</div>
            </div>
        </div>
    </div>
      <div class="bottomside w-[95%] h-[85%]  justify-between flex " :class="{'setblur' : $store.state.BlurToggle}">
          <div class="h-full w-[20%]  rounded-lg interacts">
              <userreports v-if="$store.state.UserType == 'user'"></userreports>
              <adminpage v-if="$store.state.UserType == 'admin'"></adminpage>
          </div>
          <div class="h-full w-[78.5%]  rounded-lg " :class="{'interacts' : $store.state.Page != 'AdminChat'}">
              <usercreatereport v-if="$store.state.UserType == 'user'"></usercreatereport>
              <adminreportlist v-if="$store.state.UserType == 'admin'"></adminreportlist>
          </div>
      </div>
</div>

</template>

<script>

import userreports from "./components/userpage.vue"
import usercreatereport from "./components/usercreatereport.vue"
import adminpage from "./components/adminpage.vue"
import adminreportlist from "./components/adminreportlist.vue"
import $ from 'jquery';
export default {
  methods: {
    CloseReport() {
      this.$store.state.CloseReport = false
      this.$store.state.BlurToggle = false
    },  
    CloseAddPly() {
  
      this.$store.state.AddPlayer = false
      this.$store.state.BlurToggle = false
    },
    CloseReportServer() {
      const id = this.$store.state.ReportList[this.$store.state.SelectedMyReport].UniqId
      $.post(`https://m-report/CloseReport`, JSON.stringify({reportid : id}))
      this.$store.state.CloseReport = false
      this.$store.state.BlurToggle = false
      this.$store.state.Page = 'Adminlist'
      this.$store.state.SelectedMyReport = -1
    },
    AddPlyServer (){
      $.post(`https://m-report/AddPlayerToReport`, JSON.stringify({playerid : this.playerid, reportid : this.$store.state.ReportList[this.$store.state.SelectedMyReport].UniqId}))
      this.$store.state.AddPlayer = false
      this.$store.state.BlurToggle = false
    }
  },
  data () {
    return {
      image: this.$store.state.PlayerData.Photo,
      playerid : ''
    }
  },
  mounted() {
    window.addEventListener("message", (event) => {
      if (event.data.action == "open") {
        this.$store.state.Translate = event.data.language
        if (event.data.isadmin) {
          this.$store.state.UserType = "admin"
          this.$store.state.Page = "Adminlist"
        } else {
          this.$store.state.UserType = "user"
          this.$store.state.Page = "Player"
        }
        this.$store.state.PlayerData = event.data.data.PlayerData
        this.$store.state.MyReports = event.data.data.MyReports.slice().reverse()
        this.$store.state.ReportList = event.data.reports.slice().reverse()
        this.$store.state.ClaimedReports = event.data.claimedreports.slice().reverse()
        this.$store.state.OnlineAdmins = event.data.onlineadmins
        $("#app").fadeIn(300)
      } else if (event.data.action == "update") {
        this.$store.state.PlayerData = event.data.data.PlayerData
        this.$store.state.MyReports = event.data.data.MyReports.slice().reverse()
        this.$store.state.ReportList = event.data.reports.slice().reverse()
        this.$store.state.ClaimedReports = event.data.claimedreports.slice().reverse()
        this.$store.state.OnlineAdmins = event.data.onlineadmins
      }
    })
    $(document).on('keydown', function() {
            switch(event.keyCode) {
            case 27: // ESC
                $("#app").fadeOut(300)
                $.post("https://m-report/close", JSON.stringify({}))
                break;
            }
        });
  },
  name: 'App',
  components: {
    userreports,
    usercreatereport,
    adminpage,
    adminreportlist
  }
  
}
</script>

