<script>
import $ from 'jquery';
export default {
  methods: {
    test(){
      this.$store.state.active = !this.$store.state.active
    },
    ClearFootage() {
        this.showFootage = false
    },
    CloseReportx() {
      this.$store.state.CloseReport = true
      this.$store.state.BlurToggle = true
    },
    AddPlayer() {
      this.$store.state.AddPlayer = true
      this.$store.state.BlurToggle = true
    },
    ClaimReport() {
      $.post(`https://m-report/SetClaimed`, JSON.stringify({id : this.$store.state.ReportList[this.$store.state.SelectedMyReport].UniqId}))
    },
    SetPage(index) {
      if (this.$store.state.SelectedMyReport == index) {
        this.$store.state.Page = 'Adminlist'
        this.$store.state.SelectedMyReport = -1
      } else {
        this.$store.state.Page = 'AdminChat'
        this.$store.state.SelectedMyReport = index
      }
    },
    SendMessage() {
        var today = new Date();
        let date, month, year, hours, minutes;
        date = today.getDate();
        month = today.getMonth() + 1;
        year = today.getFullYear();
        hours = today.getHours();
        minutes = today.getMinutes()
        var formattime = hours+':'+minutes
        const data = {}
        data.id = this.$store.state.ReportList[this.$store.state.SelectedMyReport].UniqId
        data.messagedata = {text : this.text, photo : false, time : formattime, photosrc : ''}
        $.post(`https://m-report/SendMessage`, JSON.stringify({data}))
        this.text = ''
        var div = document.getElementsByClassName("listsearchinput")
        div.value = ''
    },
    SetAction(type) {
      const data = {}
      data.type = type
      data.id = this.$store.state.ReportList[this.$store.state.SelectedMyReport].UniqId
      $.post(`https://m-report/SetAction`, JSON.stringify({data}))
    },
    GetFilteredResult () {
      if (this.textsearch == '') {
        return this.$store.state.ReportList
      }
      return this.$store.state.ReportList.filter(data => data.Title.includes(this.textsearch))
    },
    async CopyToClipboard(index) {
      var copydata = this.$store.state.ReportList[this.$store.state.SelectedMyReport].ReporterData[index]
      const tempInput = document.createElement('input');
      tempInput.value = copydata;
      document.body.appendChild(tempInput);
      tempInput.select();
      document.execCommand('copy');
      document.body.removeChild(tempInput);
  }

    
  },
  data () {
    return {
      showFootage : false,
      text : '',
      textsearch : ''
    }
  },
  name: 'App',
  components: {
    
  }
  
  
}
</script>

