<template>
    <div class="w-full h-full flex flex-col items-center gap-5">
        <div class="mt-10 reportshead">{{$store.state.Translate["claimedreports"]}}</div>
        <div class="w-[90%] h-[4%]  rounded-md listsearchcont flex">
            <input type="text" class="w-[80%] h-full bg-transparent outline-none ml-5 listsearchinput" v-model="text" :placeholder="$store.state.Translate['searchmyreports']" name="search">
            <div class="w-[20%] h-full flex justify-center items-center">
                <i class="fas fa-search"></i>
            </div>
        </div>
        <div  class="w-[90%] h-[70%]  mt-3 overflow-auto"  >
              <div v-for="(item, index) in GetFilteredResult()"  @click="SetPage(index)"   class="w-full h-[8%] rounded-md mt-3  reportlist flex items-center justify-between" :class="{'selected': $store.state.SelectedMyReport == index}" v-bind:id="item.Page" >
                  <p   class="itemlabel ml-3 flex items-center w-[90%] h-full">{{ item.Title }} &#x2022 {{ item.PlayerName }}</p>  
                  <p v-if="item.ReadAll" class="itemlabel ml-2 flex items-center w-[25%] h-full">&#x2022 {{ item.ShowId }}</p>
                  <div v-if="!item.ReadAll" class="unread ml-2 flex items-center w-[11%] h-[60%] rounded-full  justify-center" style="margin-right: 2.1vh;">1</div>
              </div>
        </div>
    </div>
</template>


<script>

export default {
  methods: {
    SetPage(index) {
      if (this.$store.state.SelectedMyReport == index) {
        this.$store.state.Page = 'Adminlist'
        this.$store.state.SelectedMyReport = -1
      } else {
        this.$store.state.Page = 'AdminChat'
        this.$store.state.SelectedMyReport = index
      }
    },  
    GetFilteredResult () {
      if (this.text == '') {
        return this.$store.state.ClaimedReports
      }
      return this.$store.state.ClaimedReports.filter(data => data.Title.includes(this.text))
    }
  },
  data () {
    return {
      text : ''
    }
  },
  name: 'App',
  components: {
    
  }
  
  
}


</script>

<style>
  .unread {
    background: #FF6464;
    font-family: 'Proxima Nova';
    font-style: normal;
    font-weight: 700;
    font-size: 1.5vh;
    line-height: 24px;
    letter-spacing: 0.03em;

    color: #FFFFFF;
  }
</style>