                    <div  class="w-full messagebox-text mt-2">{{item.Time}}</div>
                </div> 
            </div>
            <div v-if="this.showFootage" @click="ClearFootage" class="w-[65%] h-[50%] fixed  footagediv">
                <img :src="this.showFootage">
            </div>
        </div>
        <div class="w-[95%] h-[7%] typecontainer flex justify-between" v-if="$store.state.MyReports[$store.state.SelectedMyReport].Status == false">
            <input  type="text" class="w-[85%] h-full bg-transparent outline-none ml-5 listsearchinput" v-on:keyup.enter="SendMessage" :placeholder="$store.state.Translate['textuser']" name="search" v-model="text">
            <div class="w-[10%] h-full  flex justify-center items-center gap-3">
                <div class="w-[40%] h-[80%]  typebuttons flex justify-center items-center">
                    <i class="fas fa-camera" @click="TakePhoto"></i>
                </div>
                <div class="w-[40%] h-[80%]  typebuttons flex justify-center items-center" @click="SendMessage">
                    <i class="fas fa-location-arrow"></i>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import $ from 'jquery';
export default {
  methods: {
    SetType(val) {
      this.$store.state.Page = val
    },
    ClearFootage() {
        this.showFootage = false
    },
    CreateReport (){
        var today = new Date();
        let date, month, year, hours, minutes;
        date = today.getDate();
        month = today.getMonth() + 1;
        year = today.getFullYear();
        hours = today.getHours();
        minutes = today.getMinutes()
          date = date
              .toString()
              .padStart(2, '0');

          month = month
              .toString()
              .padStart(2, '0');
            var format = date+'.'+month+'.'+year
            var formattime = hours+':'+minutes
            const data = {title : this.reportname, type : this.$store.state.Translate[this.$store.state.Page], page : this.$store.state.Page, date : format, time : formattime}
            $.post(`https://m-report/callbackFromNui`, JSON.stringify({data}))
            this.reportname = "";
    },
    TakePhoto() {
        var _this = this
        $("#app").fadeOut(300)
        $.post('http://m-report/takePhoto', JSON.stringify({}), function(data){
            if (data == "nil") {
                _this.photoSrc = ''
                _this.photo = false
                $("#app").fadeIn(300)
            } else {
                _this.photoSrc = data
                _this.photo = true
                $("#app").fadeIn(300)
            }
        }) 
    },
    SendMessage() {
        var today = new Date();
        let date, month, year, hours, minutes;
        date = today.getDate();
        month = today.getMonth() + 1;
        year = today.getFullYear();
        hours = today.getHours();
        minutes = today.getMinutes()
        var formattime = hours+':'+minutes
        const data = {}
        data.id = this.$store.state.MyReports[this.$store.state.SelectedMyReport].UniqId
        data.messagedata = {text : this.text, photo : this.photo, time : formattime, photosrc : this.photoSrc}
        $.post(`https://m-report/SendMessage`, JSON.stringify({data : data}))
        this.photo = false
        this.photoSrc = ''
        this.text = ''
        var div = document.getElementsByClassName("listsearchinput")
        div.value = ''
    },
    GetString() {
       if (this.$store.state.MyReports[this.$store.state.SelectedMyReport].Status == true) {
        return this.$store.state.Translate["status"]['true']
       } else {
        return this.$store.state.Translate["status"]['false']
       }
    }
  },
  data () {
    return {
        showFootage : false,
        reportname : '',
        photoSrc : '',
        photo : false,
        text : ''
    }
  },
  mounted() {
    
  },
  name: 'App',
  components: {
    
  }
  
  
}
</script>

